import {hex_md5} from '../../static/js/md5.js'
import requestUtils from '../../utils/request.js'
import SInput from '@/components/s-input/s-input.vue'
export default {
	components:{
		SInput
	},
	data() {
		return {
			password:"password",
			username:'',
			passwordValue:'',
			rempass: false,
			title: 'Hello',
			btnlogin: false
		}
	},
	onShow(){
		var UserInfo = uni.getStorageSync("UserInfo")
		this.passwordValue = UserInfo.password
		this.username = UserInfo.username
		this.$refs.uninput.setValue(this.username);
		if(this.username!='' && this.username){
			this.rempass = true
		}
	},
	methods: {
		changePass(rempass){
			this.rempass = !rempass
		},
		getInputValue(value){
			this.username = value
		},
		pass(){
			this.password = (this.password == "password"?"text":"password")
		},
		login(e) {
			this.btnlogin = true
			//判断是否已经输入用户名和密码
			if (!this.username || !this.passwordValue) {
				uni.showToast({
					title: '请输入登录信息',
					icon: 'none'
				})
				this.btnlogin = false
				return
			}
			uni.request({
				url: requestUtils.BASE_URL + "/login/login.do",
				data: {
					"mobile": this.username,
					"password": hex_md5(this.passwordValue),
					"ValCode": '1234',
					"kind": "1"
				},
				success: (res) => {
					if (res.data.success) {
						uni.setStorageSync('SESSION', res.header["Set-Cookie"].split(";")[0]);
						//在跳转之前询问是否记住密码
						if (this.rempass) {
							//这里将登录信息存到缓存中
							uni.setStorageSync('UserInfo', {username:this.username,password:this.passwordValue});
						}
						uni.reLaunch({
							url: '/pages/waitoffer/waitoffer'
						});

					} else {
						uni.showToast({
							title: res.data.msg,
							icon: 'none'
						})
						this.btnlogin = false
					}
				},
				//接口失败
				fail:(err)=>{
					uni.showToast({
						title:'请求接口失败',
						icon :'none'
					})
					reject(err)
				}
			})
		}
	}
}
